import React, { Component } from "react";
import Input from "./Input";
import Joi, { errors, options } from "joi-browser";
import "./stylingform.css";

class FormMain extends Component {
  state = {
    data: {
      username: "",
      password: "",
      name: "",
    },
    errors: {},
    info: false,
  };
  schema = {
    username: Joi.string().required().label("Username"),
    password: Joi.number().required().label("Password"),
    name: Joi.string().required().label("Name"),
  };
  onChange = ({ currentTarget: input }) => {
    this.state.info = false;
    const data = { ...this.state.data };
    data[input.name] = input.value;
    const errors = {};
    const errorMessage = this.validateProperty(input);
    if (errorMessage) {
      errors[input.name] = errorMessage;
    } else delete errors[input.name];
    this.setState({ data, errors });
  };
  validateProperty = ({ name, value }) => {
    const obj = { [name]: value };
    const schema = { [name]: this.schema[name] };
    const { error } = Joi.validate(obj, schema);
    return error ? error.details[0].message : null;
  };
  validate = () => {
    const errors = {};
    const options = { abortEarly: false };
    const { error } = Joi.validate(this.state.data, this.schema, options);
    if (!error) return null;
    for (let item of error.details) {
      errors[item.path[0]] = item.message;
    }
    return errors;
  };
  handleSubmit = (e) => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    this.state.info = true;
  };
  renderButton = (label) => {
    return (
      <button type="submit" className="btn btn-primary d-flex">
        {label}
      </button>
    );
  };
  renderInput = (label, name, type) => {
    const { data, errors } = this.state;
    return (
      <Input
        label={label}
        type={type}
        name={name}
        value={data[name]}
        error={errors[name]}
        onChange={this.onChange}
      />
    );
  };
  renderTitle = (title) => {
    return (
      <div className="form-title d-flex">
        <h2>{title}</h2>
      </div>
    );
  };
  renderInfo = () => {
    return (
      <div className=" justify-content-start der">
        <h3>
          Username:
          <span className="italic-text">{this.state.data.username}</span>
        </h3>
        <h3>
          Password:
          <span className="italic-text">{this.state.data.password}</span>
        </h3>
        <h3>
          Full name: <span className="italic-text">{this.state.data.name}</span>
        </h3>
      </div>
    );
  };
  render() {
    return (
      <div>
        <div className="d-flex justify-content-center parent-div">
          <form onSubmit={this.handleSubmit} className="form-div">
            {this.renderTitle("Registration")}
            <div className="form-group form-main">
              {this.renderInput("Username", "username", "text")}
              {this.renderInput("Password", "password", "password")}
              {this.renderInput("Name", "name", "text")}
            </div>
            {this.renderButton("Login")}
          </form>
        </div>
        <div className="d-flex justify-content-center">
          {this.state.info && this.renderInfo()}
        </div>
      </div>
    );
  }
}

export default FormMain;
